code {
  font-family: 'Space Mono', source-code-pro, Menlo, Monaco, Consolas,
    'Courier New', monospace;
}

/* removing cursors */
html {
  cursor: auto;
}

a,
button {
  cursor: pointer;
}

/* https://medium.com/@matuzo/writing-css-with-accessibility-in-mind-8514a0007939 */
.nes-pointer {
  cursor: pointer;
}

.nes-btn {
  cursor: pointer;
}

.nes-radio + span {
  cursor: pointer;
}
.nes-checkbox + span {
  cursor: pointer;
}
.nes-select select {
  cursor: pointer;
}

.nes-container.pr0 {
  padding-right: 0;
  padding-left: 0;
  padding-bottom: 0;
}
.nes-container.pr0 > :not(.title) {
  padding-left: 2rem;
}

.min-w5 {
  min-width: 16rem;
}

.mw8-ml {
  max-width: 64rem;
}

/* @media (min-width: 1109px) {
  .mw8-ml {
    max-width: 1109px;
  }
} */

.hover-success:hover {
  color: #209cee;
}
.hover-error:hover {
  color: #ce372b;
}

.nes-list.is-disc li::before {
  color: inherit;
}

.fixed-width {
  width: 90px;
}

.table-design td,
.table-design th {
  padding: 0 0.25rem;
  border-right: 1px solid #e0e0e0;
}
.table-fixed-ml {
  width: 100%;
}

.table-fixed-ml tbody th {
  min-width: 200px;
}

